<template>
  <div>
    <div class="dropdown">
      <button class="btn btn-secondary dropdown-toggle" type="button" id="status-dropdown" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        Status
      </button>
      <p >Status: {{ selectedStatus }}</p>
      <div class="dropdown-menu" aria-labelledby="status-dropdown">
        <a class="dropdown-item" href="#" @click="selectedStatus = 'Confirmed', isCardVisible = false">Confirmed</a>
        <a class="dropdown-item" href="#" @click="selectedStatus = 'Dead', isCardVisible = false">Dead</a>
        <a class="dropdown-item" href="#" @click="selectedStatus = 'Recovered', isCardVisible = false">Recovered</a>
      </div>
    </div>
    <div class="form-group">
      <label for="n-countries-input">Number of countries</label>
      <input type="number" min="1" max="10" value="5" class="form-control" v-model="selectedNumCountries"
        @change="isCardVisible = false" id="n-countries-input">
    </div>
    <button class="btn btn-primary" @click="onSearch">Search</button>
    <hr>
    <Card v-if="isCardVisible" :status="selectedStatus" :ranking="ranking" />
  </div>
</template>

<script>
import Card from './components/Card.vue'

export default {
  name: 'App',
  components: {
    Card
  },
  data() {
    return {
      countries: [],
      ranking: [],
      selectedStatus: 'Confirmed',
      selectedNumCountries: 5,
      isCardVisible: false
    }
  },
  methods: {
    onSearch() {
      this.isCardVisible = true;
      this.ranking = this.findTopCountriesByStatus(this.selectedStatus);
    },
    findTopCountriesByStatus(status) {
        switch(status) {
          case "Confirmed"  : return this.sortedNewConfirmed.slice(0, this.selectedNumCountries);
          case "Dead"       : return this.sortedNewDeaths.slice(0, this.selectedNumCountries);
          case "Recovered"  : return this.sortedNewRecovered.slice(0, this.selectedNumCountries);
          default           : return [];
        }
    }
  },
  computed: {
    sortedNewConfirmed() {
      return this.countries.length && this.countries.sort( function(a, b) {
        return b.NewConfirmed - a.NewConfirmed
      });
    },
    sortedNewDeaths() {
      return this.countries.length && this.countries.sort( function(a, b) {
        return b.NewDeaths - a.NewDeaths
      });
    },
    sortedNewRecovered() {
      return this.countries.length && this.countries.sort( function(a, b) {
        return b.NewRecovered - a.NewRecovered
      });
    }
  },
  async mounted() {
    try {
      const result = fetch(`https://api.covid19api.com/summary`);
      const response = await result;
      const data = await response.json();
      this.countries = data.Countries;
      console.log(this.countries)
    }
    catch (err) {
      console.log(err);
    }
  }
}
</script>

