<template>
  <div class="card" style="width: 18rem;">
    <img :src="imageSrc" class="card-img-top" alt="">
    <div class="card-body">
        <h5 class="card-title">{{ status }}</h5>
        <h6 class="card-subtitle mb-2 text-muted">{{ new Date(Date.now()).toLocaleString() }}</h6>
        <table class="table">
        <thead>
            <tr>
            <th scope="col">#</th>
            <th scope="col">Country</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(row, index) in ranking" :key="index">
                <th scope="row">{{ index + 1 }}</th>
                <td>{{ row.Country }}</td>
            </tr>
        </tbody>
        </table>
    </div>
  </div>
</template>

<script>
export default {
    name: "Card",
    props: ["status", "ranking"],
    data() {
        return {
            imageSrc: (this.status === "Confirmed")
                ? "https://www.philips.pt/c-dam/b2bhc/master/landing-pages/experience-catalog/sleep-and-respiratory-care/how-philips-is-globally-addressing-the-coronavirus-covid-19/nurse-with-intubated-patient-l-m-s.jpg"
                : (this.status === "Dead")
                    ? "https://images.theconversation.com/files/328803/original/file-20200417-152581-1rkloss.jpg?ixlib=rb-1.1.0&rect=8%2C20%2C2779%2C1835&q=45&auto=format&w=926&fit=clip"
                    : "https://www.denverpost.com/wp-content/uploads/2020/04/Recovered-COVID-19-Patient-UCHealth-1.jpg?w=862"
        }
    }
}
</script>

<style></style>